import React from 'react';
import {Carousel} from 'react-bootstrap';
import Cards1 from './Cards1';
import Cards2 from './Cards2';

const CarouselHarryP = () => {
    return(
        <Carousel>
            <Carousel.Item>
                <Cards1 />
            </Carousel.Item>
            <Carousel.Item>
                <Cards2 />
            </Carousel.Item>
        </Carousel>
    )
}
export default CarouselHarryP